import React, { Component } from 'react';
import { connect } from 'react-redux';
import classNames from 'classNames'
import ProductListAddition from './ProductListAddition'

export default class QuanBatchDispatchRule extends Component {
	constructor(props) {
		super(props);
		// this.handleIsAutoOnlineChanged = this.handleIsAutoOnlineChanged.bind(this);
		this.handleUserLimitChanged = this.handleUserLimitChanged.bind(this);
		let {isAutoOnline = true, applyProductType = 0} = this.props;
		this.state = {
			applyProductType
		}
	}

	handleUserLimitChanged(e){
		this.setState({
			applyProductType: e.currentTarget.value
		});
	}

	render() {
		const {applyProductType} = this.state;
		const {isAutoOnline = true, productLimitList, deleteQuanBatchCreationDispatchProductLimit} = this.props;
		const listAdditionProps = {productLimitList, deleteQuanBatchCreationDispatchProductLimit};

		return (
			<div>
				<h1>发放规则</h1>
				<div>
					领取开始时间：
					<input type="text" ref="useBeginTime" />
					0点 至 
					<input type="text" ref="useEndTime" />
					24点结束
				</div>
				<div>
					自动上架：<input type="checkbox" defaultChecked={isAutoOnline} ref="isAutoOnline" />是
				</div>
				<div>
					批次图片：
					<input type="file"/>
				</div>
				<div>
					发放数量：
					<input type="text" />张
				</div>
				<div>
					数量限制：每用户限领<input type="text" />张
				</div>
				<div>
					用户限制：
					<input type="radio" name="userLimit" value="0" checked={applyProductType == 0} onChange={this.handleUserLimitChanged}/>无限制
					<input type="radio" name="userLimit" value="1" checked={applyProductType == 1} onChange={this.handleUserLimitChanged}/>指定班级用户
					{applyProductType == 1 ? <ProductListAddition {...listAdditionProps} /> : null}
				</div>
			</div>
		);
	}
}


