export const DELETE_QUAN_BATCH_CREATION_DISPATCH_PRODUCT_LIMIT = 'DELETE_QUAN_BATCH_CREATION_DISPATCH_PRODUCT_LIMIT';

export function deleteQuanBatchCreationDispatchProductLimit(index) {
  return {
    type: DELETE_QUAN_BATCH_CREATION_DISPATCH_PRODUCT_LIMIT,
    index
  };
}

export const ADD_QUAN_BATCH_CREATION_DISPATCH_PRODUCT_LIMIT = 'ADD_QUAN_BATCH_CREATION_DISPATCH_PRODUCT_LIMIT';

export function addQuanBatchCreationDispatchProductLimit(productList) {
  return {
    type: ADD_QUAN_BATCH_CREATION_DISPATCH_PRODUCT_LIMIT,
    productList
  };
}


